body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.loader-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
}

.loader-wrapper-dark {
    background-color: #0f0f14;
}

.page-loader {
    width: 96px;
    height: 96px;
    border: 12px solid #f6f6f8;
    border-radius: 50%;
    border-top: 12px solid #009fed;
    -webkit-animation: spin 1.5s linear infinite;
    animation: spin 1.5s linear infinite;
}

.page-loader-dark {
    border: 12px solid rgba(245, 245, 247, 0.04);
    border-top: 12px solid #009fed;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}